} */

/* ::selection {
  background-color: black;
} */

input[type="text"], textarea {
  background-color: inherit;
}

.applet {
  padding: 1em;
  height: 100%;
  width: 100%;
}

.applet-container>p {
  text-align: center;
  justify-content: center;
}

.applet>h2 {
  text-align: center;
}


.code {
  display: block;
  unicode-bidi: embed;
  font-family: monospace;
  white-space: pre;
  padding: 1em;
  margin: 10px;
}


/* About me section */

#headshot {
  position: center;
  padding-top: 25%;
  width: 25%;
  max-width: 90%;
  height: 25%;
  max-height: 90%;
  background-image: url("../public/pics/headshot2.jpg");
  border-radius: 50%;
  background-size: cover;
  margin: auto;
  margin-top: 2%;
  margin-bottom: 2%;
  transform: scale(-1, 1)
}

ul#interests {
  text-align: left;
  display: table;
  margin: 0 auto;
}


/* Loan Affordability grids */
.grid {
  white-space: nowrap;
}

.grid-element {
  display: inline-block;
  height: 0;
  width: 3%;
  padding-bottom: 3%;
}

.grid-element:hover {
  opacity: 0.5;
}


/* JSON Schema viewer graph */
.node text {
  transform: rotate(-90deg);
}

path.link {
  fill: none;
}


/* Credits section */
#creds img {
  max-width: 20px;
  height: 20px;
}

#creds a {
  background-color: transparent;
  text-decoration: none;
}

#creds div.ul {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

#creds div.li {
  margin: 2px;
  padding: 2px;
  min-width: 150px;
  flex: 1;
}

#creds blockquote {
  text-align: center;
}
